<template>
  <div>
    <div class="container-fluid p-0">
      <div class="row d-flex justify-content-center m-0">
        <div
          class="col-12 d-flex justify-content-center quote-block row m-0 p-0"
        >
          <!-- Header -->
          <div
            class="
              col-12
              py-5
              test-title
              px-2
              d-flex
              align-items-center
              justify-content-center
            "
          >
            <h2 class="text-center">The Shop</h2>
          </div>

          <!-- Items -->
          <div
            class="
              col-12 col-md-9
              pb-5
              mb-5
              p-0
              row
              item
              d-flex
              justify-content-between
              align-items-start
            "
            v-for="(item, index) in shopItems"
            :key="index"
          >
            <VideoItem :item="item" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import VideoItem from "@/components/shop/sections/VideoItem.vue";

export default {
  components: {
    VideoItem,
  },

  name: "TestimonialPage",
  data() {
    return {
      shopItems: [],
    };
  },
  methods: {
    getContent() {
      this.$prismic.client
        .query(this.$prismic.predicate.at("document.type", "shop_item"))
        .then((response) => {
          console.log(response);
          this.shopItems = response.results;

          this.initSnipcart();
        });
    },

    initSnipcart() {
      document.addEventListener("snipcart.ready", () => {
        window.Snipcart.events.on("cart.confirmed", (response) => {
          console.log(response);
          console.log("confirmed");
        });
      });
    },
  },

  mounted() {
    this.getContent();
  },
};
</script>

